@import '../_variables';
@import '../global/fonts';
@font-face {
    font-family: "Roboto-Black Roboto-Bold Roboto-Regular";
    src: url('../global/_fonts.scss');
}

* {
    box-sizing: border-box;
}

html{
    scroll-behavior: smooth;
    font-size: calc(100vw / 1920 * 10);
    // @media (max-width: 1024px) {
    //     font-size: calc(100vw / 1024 * 10)
    // }
    // @media (max-width: 767px) {
    //     font-size: calc(100vw / 767 * 10)
    // }
    // @media (max-width: 500px) {
    //     font-size: calc(100vw / 500 * 10)
    // }
}

body {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

div {
    width: 100%;
    text-align: center;
}

div>img{
    width: 100%;
    height: auto;
}

h1 {
    font-family: 'Roboto-Bold';
}

.wrapper{
    max-width: 140rem;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
    margin: 1rem auto;
    font-size: calc(100% + 0.5vw);
}


.line {
    background-color: $base;
    // width: calc(100% - 50rem);
    width: 140rem;
    height: 0.5rem;
    border-style: none;
}

.container {
    max-width: 160rem;
	min-width: 35rem;
	padding: 0 7.5rem;
	margin: 0 auto;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
}


ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
}